import axios, { AxiosRequestConfig } from "axios";
import { jwtDecode } from "jwt-decode"; //  
import { useNavigate } from "react-router";
import { toast } from "react-toastify";

export async function getTokenFromLocalStorage(
  key: string,
): Promise<string> {
  try {
    const data = localStorage.getItem(key);
    return data ? JSON.parse(data) : "";
  } catch (error) {
    console.error("Error getting token from localStorage:", error);
    return "";
  }
}

export async function setTokenToLocalStorage(
  key: string,
  token: string,
): Promise<void> {
  try {
    localStorage.setItem(key, JSON.stringify(token));
  } catch (error) {
    console.error("Error setting token to localStorage:", error);
  }
}

export async function removeTokenFromLocalStorage(
  key: string,
): Promise<void> {
  try {
    localStorage.removeItem(key);
  } catch (error) {
    console.error("Error removing token from localStorage:", error);
  }
}
export const getTokenExpiration = (token: string): string | null => {
  try {
    const decoded: { exp: number } = jwtDecode(token);
    const expirationTime = new Date(
      decoded.exp * 1000,
    ).toLocaleString(); //    
    return expirationTime;
  } catch (error) {
    console.error("   :", error);
    return null;
  }
};

//    access  refresh 
const getAccessTokenExpiration = () => {
  const accessToken = localStorage.getItem("accessToken");
  return accessToken ? getTokenExpiration(accessToken) : null;
};

const getRefreshTokenExpiration = () => {
  const refreshToken = localStorage.getItem("refreshToken");
  return refreshToken ? getTokenExpiration(refreshToken) : null;
};

//      
console.log(
  "  access :",
  getAccessTokenExpiration(),
);
console.log(
  "  refresh :",
  getRefreshTokenExpiration(),
);
const refreshToken = async (): Promise<any> => {
  const refresh = localStorage.getItem("refreshToken");

  const { data } = await axios.post(
    "https://api.lr45981.tw1.ru/api/v1/token/refresh/",
    {
      refresh,
    },
  );

  return data.access;
};
export const isTokenExpired = (token: string): boolean => {
  try {
    const decoded: { exp: number } = jwtDecode(token);
    const currentTime = Date.now() / 1000;
    console.log(new Date(decoded.exp * 1000).toLocaleString());

    return decoded.exp < currentTime;
  } catch (error) {
    console.error("   :", error);
    return true;
  }
};
export const logoutProfile = () => {
  localStorage.removeItem("accessToken");
  localStorage.removeItem("refreshToken");
};
const getAccessToken = () => localStorage.getItem("accessToken");
const getRefreshToken = () => localStorage.getItem("refreshToken");
const tokenref = getRefreshToken();
const tokenacc = getAccessToken();

console.log(isTokenExpired(tokenref!));
console.log(isTokenExpired(tokenacc!));

export const checkTokens = (): boolean => {
  const accessToken = getAccessToken();
  const refreshToken = getRefreshToken();

  if (accessToken && isTokenExpired(accessToken)) {
    if (refreshToken && isTokenExpired(refreshToken)) {
      return false; // logout
    } else {
      return true; //  
    }
  }
  return true; //  
};
export const axiosWithRefreshToken = async <T>(
  url: string,
  options?: AxiosRequestConfig,
): Promise<T> => {
  if (!checkTokens()) {
    console.log(" ,   ");

    logoutProfile();
    setTimeout(() => {
      toast.error("  ");
    }, 2000);
    return Promise.reject("   ");
  }

  //   
  const accessToken = getAccessToken();
  options = {
    ...options,
    headers: {
      ...options?.headers,
      Authorization: `Bearer ${accessToken}`,
    },
  };

  try {
    const response = await axios(url, options);
    return response.data;
  } catch (error: any) {
    if (
      error.response?.status === 401 &&
      isTokenExpired(getAccessToken()!)
    ) {
      //   ,   
      const refresh = getRefreshToken();

      if (refresh && !isTokenExpired(refresh)) {
        try {
          const refreshData = await refreshToken();
          localStorage.setItem("accessToken", refreshData);

          //       accessToken
          options.headers!.Authorization = `Bearer ${refreshData}`;
          const retryResponse = await axios(url, options);
          return retryResponse.data;
        } catch (refreshError) {
          console.error(
            "   :",
            refreshError,
          );
          logoutProfile();
          // window.location.href = "/"; //     ,    

          return Promise.reject("  ");
        }
      } else {
        console.error("  ");
        toast.error("  ");
        // setTimeout(() => {
        //   window.location.href = "/";
        // }, 2000); //   
        return Promise.reject("Refresh token is expired or missing");
      }
    }

    throw error;
  }
};
